/* GamePage.css */

/* Ï†ÑÏ≤¥ Î∞∞Í≤Ω Î∞è Ìè∞Ìä∏ */
body {
  margin: 0;
  background-color: #ffffff;
  font-family: "Helvetica Neue", sans-serif;
  color: #000000;
}

/* Í≤åÏûÑ Ïª®ÌÖåÏù¥ÎÑà */
.game-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 20px;
}

/* ÌÉÄÏù¥ÌãÄ (ÏÑ†ÌÉù) */
.page-title {
  font-size: 32px;
  font-weight: bold;
  margin-bottom: 30px;
  color: #000;
}

/* ÏñºÏùå Î∏îÎ°ù */
.ice-block {
  position: relative;
  width: 500px;
  height: 500px;
  background-color: #ffffff;
  border: 4px solid #000000;
  border-radius: 12px;
  margin: 30px auto;
  transition: border-radius 0.3s ease, transform 0.2s ease;
}

.ice-block:hover {
  transform: scale(1.02);
}

.ice-block.game-over {
  background-color: transparent;
  border: none;
}

/* Í≤åÏûÑ Í≤∞Í≥º Î©îÏãúÏßÄ */
.gameover-message,
.ice-message {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #ffffff;
  font-size: 19px;
  font-weight: 900;
  text-align: center;
  z-index: 10;
  white-space: nowrap;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000,
    1px 1px 0 #000;
}

/* ÎèÑÍµ¨ Î≤ÑÌäº Î¨∂Ïùå */
.tool-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 11px;
  margin-top: 30px;
  justify-content: center;
}

/* ÎèÑÍµ¨ Î≤ÑÌäº */
.tool-button {
  outline: 2px solid #000000;
  background-color: #ffffff;
  color: #000000;
  border: none;
  padding: 5px 18px;
  border-radius: 8px;
  font-size: 14px;
  cursor: pointer;
  transition: transform 0.2s ease;
}

.tool-button:hover {
  transform: scale(1.1);
}

.tool-button.selected {
  outline: 2px solid #000000;
  background-color: #ffffff;
  color: #000000;
}

/* ÏÑ†ÌÉùÌïú ÎèÑÍµ¨ ÌÖçÏä§Ìä∏ */
.selected-tool-text {
  margin-top: 15px;
  font-size: 16px;
  font-weight: bold;
  color: #000000;
}

/* HP ÌëúÏãú */
.status-text {
  font-size: 18px;
  margin-bottom: 20px;
  font-weight: 600;
}

/* Ï†úÏ∂ú Î≤ÑÌäº */
.submit-button {
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
  background-color: #000000;
  color: #ffffff;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: transform 0.2s ease;
}

.submit-button:hover {
  transform: scale(1.1);
}

/* Îã§Ïãú ÏãúÏûë Î≤ÑÌäº */
.play-again-button {
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 8px;
  border: none;
  background-color: #000000;
  color: #ffffff;
  cursor: pointer;
  transition: transform 0.2s ease;
}

.play-again-button:hover {
  transform: scale(1.1);
}

/* Ïª§ÏÑú Ïä§ÌÉÄÏùº */
.cursor-axe {
  cursor: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='70' height='84' style='fill:black;font-size:42px;'><text y='50%'>ü™ì</text></svg>")
      16 0,
    auto;
}

.cursor-hammer {
  cursor: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='70' height='84' style='fill:black;font-size:42px;'><text y='50%'>üî®</text></svg>")
      16 0,
    auto;
}

.cursor-chisel {
  cursor: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='70' height='84' style='fill:black;font-size:42px;'><text y='50%'>üîß</text></svg>")
      16 0,
    auto;
}

.cursor-awl {
  cursor: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='70' height='84' style='fill:black;font-size:42px;'><text y='50%'>ü™õ</text></svg>")
      16 0,
    auto;
}

.color-grid-inside {
  position: absolute;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(100, 1fr);
  grid-template-rows: repeat(100, 1fr);
  z-index: 0;
  top: 0;
  left: 0;
  overflow: hidden;
}

.grid-cell-inside {
  width: 100%;
  height: 100%;
}

/* ÌùîÎì§Î¶º Ïï†ÎãàÎ©îÏù¥ÏÖò */
@keyframes shake {
  0% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(-4px);
  }
  50% {
    transform: translateX(4px);
  }
  75% {
    transform: translateX(-2px);
  }
  100% {
    transform: translateX(0);
  }
}

.shake {
  animation: shake 0.2s;
}

/* ÏñºÏùå Ï°∞Í∞Å Îñ®Ïñ¥ÏßÄÎäî Ïï†ÎãàÎ©îÏù¥ÏÖò */
.ice-flake {
  position: absolute;
  width: 5px;
  height: 5px;
  animation: flake-fall 0.4s ease-out forwards;
  z-index: 15;
  opacity: 0.8;
}

@keyframes flake-fall {
  0% {
    transform: translateY(0);
    opacity: 0.8;
  }
  100% {
    transform: translateY(80px);
    opacity: 0;
  }
}
